export default {
	data() {
		return {
			isCard: true,
			TabCur: 0,
			animation:'',
			list: [{
					name: 'fade',
					color: 'red'
				},
				{
					name: 'scale-up',
					color: 'orange'
				},
				{
					name: 'scale-down',
					color: 'olive'
				},
				{
					name: 'slide-top',
					color: 'green'
				}, {
					name: 'slide-bottom',
					color: 'cyan'
				},
				{
					name: 'slide-left',
					color: 'blue'
				},
				{
					name: 'slide-right',
					color: 'purple'
				},
				{
					name: 'shake',
					color: 'mauve'
				}
			],
			toggleDelay: false

		};
	},
	methods: {
		Toggle(e) {
			var anmiaton = e.currentTarget.dataset.class;
			this.animation= anmiaton;
			setTimeout(()=>{
				this.animation= '';
			}, 1000)
		},
		ToggleDelay() {
			this.toggleDelay= true;
			setTimeout(()=>{
				this.toggleDelay= false
			}, 1000)
		},
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
			this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
		},
		myset(e){
			uni.navigateTo({
				url: '/pages/main_page/myset/myset',
				animationType: 'pop-in',
				animationDuration: 200
			});
		}
	},
}
